import React, { render, FC, Suspense } from 'react';
import { CommonProps } from '@interface';
import { ItemClickType } from '@enum';
import AdIcon from '../AdIcon';
import '@assets/styles/global.less';
import '@assets/styles/animate.less';
import './index.less';

const Card: FC<CommonProps> = (props = {}) => {
    const {
        schema,
        info,
        schema: {
            className = '',
            site = '',
            blurRadius = 0,
            isShowBg = false, // 是否展示背景
            hasMask = false,
            adIconPos = '', // left-bottom / left-top / right-top / right-bottom
            adIconStyle = '', // left-bottom / left-top / right-top / right-bottom
            children = [],
            onClick = () => {},
            logInfo: { itemClickType = ItemClickType.CARD_BLANK_CLICK } = {},
        },
        style = {},
        info: { isPorttrait, materialFeature = [] },
        render,
    } = props;

    const backgrounds: string[] = materialFeature.map((item) => `url(${item.coverUrl})`);

    const bgStyle = {
        backgroundImage: backgrounds[0],
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat',
        filter: `blur(${blurRadius}px)`,
    };
    const landscapeRepeatList = new Array(3).fill(0);
    const imageSize = materialFeature[0]?.materialSize;
    const isHorizontal = imageSize.width > imageSize.height;

    // 横版并且图片是长图，则需要平铺3张
    const isMulti = !isPorttrait && !isHorizontal;

    // 如果是竖屏，并且是水平图片，则需要居中展示
    if (isPorttrait && isHorizontal) {
        bgStyle.backgroundSize = 'contain';
    }

    return (
        <div>
            {/* 背景 */}
            {isShowBg ? (
                <div
                    className="background-wrap"
                    data-itemClickType={ItemClickType.BLANK_AREA_CLICK}
                >
                    {!isMulti && <div className="background-porttrait" style={{ ...bgStyle }} />}
                    {isMulti &&
                        landscapeRepeatList.map((item, i) => {
                            return (
                                <div
                                    key={i}
                                    className="background-landscape"
                                    style={{ ...bgStyle }}
                                />
                            );
                        })}
                    {hasMask && (
                        <div
                            className="background-cover"
                            style={{ backgroundColor: 'rgba(0, 0, 0, 0.7)' }}
                        />
                    )}
                </div>
            ) : null}
            <div
                className={`card ${className} ${site}`}
                onClick={onClick}
                style={style}
                data-itemClickType={itemClickType}
            >
                {Array.isArray(children) && children.length
                    ? children.map((item, index) => {
                        return render({ node: item });
                    })
                    : null}
            </div>
            {/* 广告标在外侧 配置位置 */}
            {adIconPos ? (
                <div className={adIconPos}>
                    <AdIcon schema={{ ...schema, posType: 'outside' }} info={info} />
                </div>
            ) : null}
        </div>
    );
};
export default Card;
